import React,{Component} from 'react';
// import querystring from 'query-string'
import Hot from '../components/Hot/Hot';
import Cell from '../components/Cell/Cell';
import { Carousel } from 'antd-mobile';
import querystring from 'query-string';
export default class Detail extends Component{
    state={
        data:{
          /*detail:{
            auth_icon:null
          }*/
        },
        val: ['0', '1'],
        imgHeight: 200,
      };
      componentDidMount(){
        // let {history,match,location} = this.props;
        console.log(this.props.match.params)
        // console.log(2,querystring.parse(location.search).dataName)

    
        console.log(this.props)
        let id = this.props.match.params.id;
        let dataName = querystring.parse(this.props.location.search).dataName;
        console.log(id)
        Detail.axios({
          url:`/api/${dataName}/${id}`
        }).then(
          res=>
        //   console.log(res.data)
          this.setState({data:res.data.data})
        )
    
      }
    render(){
        let{data} = this.state
        // console.log(data)
        // let {data} = this.state;
        return (
          <div style={{fontSize: '0.24rem'}}>
          <div className="g-flexview">
              <header className="m-navbar my-header">
                  <a href="#" className="navbar-item" >
                       <i className="back-ico" onClick={()=>this.props.history.go(-1)}></i>
                  </a>
                  <div className="navbar-center">
                      <span className="navbar-title">{data.title}</span>
                  </div>
                  <div className="navbar-item nav-like">
                      <a href="#">
                          <i className="icon-like-outline"></i>
                          
                      </a>
                      <a href="#">
                          <i className="icon-qrscan"></i>
                         
                      </a>
                      <a href="#">
                          <i className="icon-share3"></i>
                         
                      </a>
                  </div>
              </header>
               {/*  mer-main */}
              <div className="mer-main g-scrollview">

                   {/*  m-slider */}
                  {/* <div className="m-slider mer-swiper" id="J_Slider">
                      <div className="slider-wrapper" style={{transform: 'translate3d(-808px, 0px, 0px)',transitionDuration: '200ms'}}><div className="slider-item" style={{width: '404px',height:'195px'}}>
                              <a href="#">
                                   <img src="/assets/img/mer_banner_01.jpg" />
                              </a>
                          </div>
                          <div className="slider-item" style={{width: '404px'}}>
                              <a href="#">
                                  <img src="/assets/img/mer_banner_01.jpg"/>
                              </a>
                          </div>
                          <div className="slider-item" style={{width: '404px'}}>
                              <a href="#">
                                    <img src="/assets/img/mer_banner_01.jpg"/>
                              </a>
                          </div>
                          <div className="slider-item" style={{width: '404px'}}>
                              <a href="#">
                                   <img src="/assets/img/mer_banner_01.jpg"/>
                              </a>
                          </div>
                      <div className="slider-item" style={{width: '404px'}}>
                              <a href="#">
                                  <img src="/assets/img/mer_banner_01.jpg"/>
                              </a>
                          </div></div>
                      <div className="slider-pagination"><span className="slider-pagination-item"></span><span className="slider-pagination-item slider-pagination-item-active"></span><span className="slider-pagination-item"></span></div>
                      </div>
                  
 */}

                  <Carousel
          autoplay={true}
          infinite
          autoplayInterval="1000"
          dots="true"
          swipeSpeed="10"
        //   beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
        //   afterChange={index => console.log('slide to', index)}
        >
          {this.state.val.map(val => (
            // console.log(value),
            <a
              key={val._id}
            
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight}}
            >
              {  data.detail &&
              <img
                src={`${Detail.baseUrl}${data.detail.auth_icon[val]}`}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
              }
            </a>
          ))}
        </Carousel>
        
                   {/*  m-slider */}

                   {/*  mer-shop-start*/}
                   {
                       data && (
                        <div className="mer-shop clearfix" >

                        <div className="mer-shop-img fl">
                      
                           <i href="#">
                           {
                             data.detail && <img 
                        //   src='/assets/img/mer_shop_logo.png'
                           src={`${Detail.baseUrl}${data.detail.logo}`} 
                           alt=""/>
                         }
                         </i>
                        </div>
                        <div className="mer-shop-js fl">
                            <h3><p>{data.title}</p><i className="icon-hezuo">合作</i></h3>
                            <div className="mer-shop-area"><i className="img-logo"><img src="/assets/img/mer_logo_area.png" alt=""/></i>
                                <p>{data.sub}</p>
                            </div>
                            <div className="mer-shop-logo"><i className="img-logo"><img src="/assets/img/mer_logo_te.png" alt=""/></i>
                                <p>{data.subt}</p>
                            </div>
                            <div className="mer-shop-logo"><i className="img-logo"><img src="/assets/img/mer_logo_fan.png" alt=""/></i>
                                <p>{data.subti}</p>
                            </div>
                            <div className="mer-shop-logo"><i className="img-logo"><img src="/assets/img/mer_logo_you.png" alt=""/></i>
                                <p>{data.subti}</p>
                            </div>
                        </div>
                        <div className="mer-shop-phone clearfix fr">
                            <div className="mer-shop-tel">
                               <i href="#"><img src="/assets/img/mer_logo_phone.jpg" alt=""/></i>
                       
                            </div>
                        </div>
                        <div className="mer-shop-pj">
                           
                           <div id="star" className="star star-1" data-num="3" title="regular" style={{width: '1.4rem'}}><img src="/assets/img/star-on.png" alt="1" title="regular"/>&nbsp;<img src="/assets/img/star-on.png" alt="2" title="regular"/>&nbsp;<img src="/assets/img/star-on.png" alt="3" title="regular"/>&nbsp;<img src="/assets/img/star-off.png" alt="4" title="regular"/>&nbsp;<img src="/assets/img/star-off.png" alt="5" title="regular"/><input type="hidden" name="score" value="3" readOnly="readonly"/></div>
                            <div className="hinit">3.0分</div>
                        </div>
                 
                    </div>
                       )
                   }
                        
                   {/*  mer-shop-end */}

                   {/*  mer-hot-start */}
      
                  {
                    [1,2,3,4,5].map((item,index)=>(
                      <Hot key={index}/>
                    ))
                  }               
                   {/*  mer-hot-end  */}

              </div>
               {/*  mer-main */}
               {/*  footer */}
           
               {/*  footer */}
          </div>
          {/* <script>
          $('.star').raty({
              starOff: '/assets/img/star-off.png',
              starOn: '/assets/img/star-on.png',
              width: '1.4rem',
              readOnly: true,
              score: function() {
                  return $(this).attr("data-num");
              },
          });
          </script> */}
           {/*  引入YDUI脚本 */}
          <script type="text/javascript" src="dist/YDUI/js/ydui.js"></script>    
      </div>
          )
    }
}