import React from 'react'

// import {Link} from 'react-router-dom'

export default class Follow extends React.Component{
    state={
        list:[],
    };
    componentDidMount(){
        Follow.axios({
            url:"api/goods",
            params:{_pages:1,_limit:6}
        }).then(
            res=>
            // console.log(res.data)
            this.setState({list:res.data.data})
        )
    }
    render(){
        let {list} = this.state
        return(
            <div style={{fontsize: '0.24rem'}}>
            <div className="g-flexview">
                 {/*  header */} 
                <div className="m-navbar my-header">
                    <a href="#" className="navbar-item">
                        <i className="back-ico"></i>
                    </a>
                    <div className="navbar-center">
                        <span className="navbar-title">商品店</span>
                    </div>
                </div>
                 {/*  header */} 
                 {/*  proMain */} 
                <div className="pro-main" style={{zIndex:999}}>
                    <div className="pro-search">
                        <div className="cell-item">
                            <div className="icon-search"></div>
                            <div className="cell-right">
                                <input type="text" className="cell-input" placeholder="输入商品关键字" autoComplete="off"/>
                                <a href="##" className="btn btn-warning">搜索</a>
                            </div>
                        </div>
                    </div>
                </div>
                 {/*  proMain */} 

                <div className="pro-scroll g-scrollview" >
                    <div className="m-scrolltab" id="J_ScrollTab" style={{marginTop:'2rem',marginBottom:'.8rem'}}>
                        <div className="scrolltab-nav">
                            <a href="##" className="scrolltab-item crt">
                                <div className="scrolltab-icon">
                                     {/*  你的图标 */} 
                                </div>
                                <div className="scrolltab-title">常用分类</div>
                            </a>
                            <a href="##" className="scrolltab-item">
                                <div className="scrolltab-icon">
                                     {/*  你的图标 */} 
                                </div>
                                <div className="scrolltab-title">服装饰品</div>
                            </a>
                            <a href="##" className="scrolltab-item">
                                <div className="scrolltab-icon">
                                     {/*  你的图标 */} 
                                </div>
                                <div className="scrolltab-title">护理化妆</div>
                            </a>
                        </div>
                        <div className="scrolltab-content">
                            <div className="scrolltab-content-item">
                                <div className="produce-banner clearfix">
                                    <a href="#">
                                       <img src="/assets/img/produce_banner.jpg" alt="" style={{width:'5.8rem'}}/>
                                   </a>
                                </div>
                                <strong className="scrolltab-content-title">常用分类</strong>
                          {
                               list.map((item)=>(

                            
                                <div className="produce-list flex" key={item._id}>
                                    <div className="produce-list-left"><a href="#"><img src={`${Follow.baseUrl}${item.picture}`} alt=""/></a></div>
                                    <div className="produce-list-right flex-1 ">
                                        <h3>{item.title}</h3>
                                        <p>{item.des}</p>
                                        <div className="price_list flex flex-1 flex-h flex-align-center">
                                            <span className="price">{item.price}</span>
                                            <del className="last">{item.pre_price} </del>

                                            <input value='加入购物车' type="button" style={{border:'none',border:'1px solid #aaa',background:'#ff9',width:'1.5rem',height:'.6rem',font:'15px/18px ""',borderRadius:'4px',marginLeft:'.6rem'}}/>

                                        </div>
                                    </div>
                                </div>
                            ))
                          }
                               
                                 {/*  对应分类内容 */} 
                            </div>
                            <div className="scrolltab-content-item">
                                <strong className="scrolltab-content-title">服装饰品</strong>
                                 {/*  对应分类内容 */} 
                         {
                               list.map((item)=>(

                                                            
                                <div className="produce-list flex" key={item._id}>
                                    <div className="produce-list-left"><a href="#"><img src={`${Follow.baseUrl}${item.picture}`} alt=""/></a></div>
                                    <div className="produce-list-right flex-1 ">
                                        <h3>{item.title}</h3>
                                        <p>{item.des}</p>
                                        <div className="price_list flex flex-1 flex-h flex-align-center">
                                        <span className="price">{item.price}</span>
                                         <del className="last">{item.pre_price} </del>
                                         <input value='加入购物车' type="button" style={{border:'none',border:'1px solid #aaa',background:'#ff9',width:'1.5rem',height:'.6rem',font:'15px/18px ""',borderRadius:'4px',marginLeft:'.6rem'}}/>
                                        </div>
                                    </div>
                                </div>
                                )) }


                            </div>
                            
                            <div className="scrolltab-content-item">
                                <strong className="scrolltab-content-title">护理化妆</strong>
                                 {/*  对应分类内容 */} 
                                <div className="scrolltab-content-item">
                               {/* <strong className="scrolltab-content-title">空调</strong> */}

                               {
                               list.map((item)=>(

                                                            
                                <div className="produce-list flex" key={item._id}>
                                        <div className="produce-list-left"><a href="#"><img src={`${Follow.baseUrl}${item.picture}`} alt=""/></a></div>
                                        <div className="produce-list-right flex-1 ">
                                        <h3>{item.title}</h3>
                                        <p>{item.des}</p>
                                            <div className="price_list flex flex-1 flex-h flex-align-center">
                                            <span className="price">{item.price}</span>
                                         <del className="last">{item.pre_price} </del>
                                         <input value='加入购物车' type="button" style={{border:'none',border:'1px solid #aaa',background:'#ff9',width:'1.5rem',height:'.6rem',font:'15px/18px ""',borderRadius:'4px',marginLeft:'.6rem'}}/>
                                            </div>
                                        </div>
                                    </div>
                               ))}
        
                                </div>
                            </div>
                        </div>
         
                    </div>
                    
                </div>

          
            </div>        
                 {/*  引入YDUI脚本 */} 
                <script type="text/javascript" src="dist/YDUI/js/ydui.js"></script>
                {/* <script>
               
        
                $('#J_ScrollTab').scrollTab({
        
                });
                </script> */}
        
    
        </div>
        )
    }
}