import React from 'react';
import './Login.css';
import {Link} from 'react-router-dom';
import asyncAction from "../store/asyncAction";
import * as types from "../store/types";
import connect from "react-redux/es/connect/connect";
class Login extends React.Component{
  constructor(){
    super();
    this.state={
      username:'',
      password:'',
      mess:''
    }
  }
  //处理多个事件双向绑定,给每个输入框加一个'name'属性
  changeMess=(ev)=>{

    this.setState({
      [ev.target.name]:ev.target.value
    })
  };
    render(){
      let {username,password}=this.state;
       console.log(this.state.mess)
        return(
            <div className='Login'>
                {/*header-start */}
               <div className="m-navbar my-header">
                    <a href="##" onClick={()=>this.props.history.go(-1)}className="navbar-item">
                        <i className="back-ico"></i>
                    </a>
                    <div className="navbar-center">
                        <span className="navbar-title">登录</span>
                    </div>
                </div>
                  {/*header-end */}
                  <div className="content">
           
        <h1 className='log_icon'></h1>
        <div className="login-box">
          <p className="lsolid"></p>
          <div className="login">

            <Link to="/login">登录</Link>         
            <span></span>
            <Link to="/reg">注册</Link>  
            
          </div>
          <p className="rsolid"></p>
        </div>
        <ul>
          <li className="lifirst">
            <input className='login_put' type="text" name='username' value={username} onChange={this.changeMess}/>
            <span>帐号</span>
          </li>
          <li>
            <input className='login_put'  type="text" name='password' value={password} onChange={this.changeMess}/>
            <span>密码</span>
          </li>
          <b style={{color:'red'}}>{this.state.mess}</b>
        </ul>
        <div className="footbox">
          <input type="button" value="登 录" className="login-btn" onClick={()=>this.props.login({
             username:this.state.username,
             password:this.state.password,
             history:this.props.history,
             _this:this

          })                   
          }/>
          <a href="##" className="tishi">忘记密码？</a>
        </div>
</div>
            </div>
        )
    }
}

const initMapDispatchToProps=(dispatch,ownProps)=>({
  login:({username,password,history,_this})=>dispatch(asyncAction({
    apiname:'login',
    method:'post',
    data:{username,password},
    typeName:types.UPDATE_USER
  })).then(
    res=>{
      if (res.err===0){
        // alert(0);
        // console.log(ownProps)
        // history.push('/user');
        ownProps.history.push('/user')
      } else {
        // console.log(res)
        _this.setState({mess:res.msg})
      }

      //同步localstorage
    }
  )
});

export default connect(
  null,
  initMapDispatchToProps
)(Login)