import {VIEW_LOADING} from './types';

export default ({apiname,params,typeName,data,method})=>(dispatch,getState)=>{
  // dispatch({type:VIEW_LOADING,payload:true});
  // console.log('asyncactions','/mock/'+apiname);
  return axios({
    url:'/api/'+apiname,
    method:method||null,
    params:params||null,
    data:data||null
  }).then(
    res=>{
      dispatch({type:typeName,payload:res.data});
      // dispatch({type:VIEW_LOADING,payload:false});
      return {err:res.data.err,msg:res.data.mess};
    }
  );
}