import React from 'react';
import {Route , Switch,Redirect}  from 'react-router-dom'
import Header from "./Header";
import Home from "../pages/Home"
import Footer from "./Footer";

import Follow from "../pages/Follow";
import User from "../pages/User";
import Cart from "../pages/Cart";
import Login from "../pages/Login";
import Reg from "../pages/Reg";
import Detail from "../pages/Detail";
import ErrorPage from "../pages/ErrorPage";
import Loading from "../components/loading/Loading";

import Auth from "../guard/Auth"

import {connect} from 'react-redux';

import {VIEW_NAV,VIEW_FOOT,VIEW_LOADING} from '../store/types'

class App extends React.Component{
  componentWillReceiveProps(nextProps){
    let path = nextProps.location.pathname;
    this.checkPath(path);
    window.scrollTo(0,0)
  }
  componentDidMount(){
    let path = this.props.location.pathname;
    this.checkPath(path)
  }
  checkPath = (path) => {
    let {viewNav,viewFoot}=this.props;
    if (/home/.test(path)){
      // this.setState({bNav:true,bFoot:true})
      viewNav(true);viewFoot(true)
    }
    if (/login|reg|detail/.test(path)){
      // this.setState({bNav:false,bFoot:false})
      viewNav(false);viewFoot(false)
    }
    if (/follow|cart|user/.test(path)){
      // this.setState({bNav:false,bFoot:true})
      viewNav(false);viewFoot(true)
    }
  };
  // state={
  //   bLoading:false
  // }
  render(){
    let {bNav,bFoot,bLoading}=this.props;
    // let{bLoading} = this.state;
    return(
      <div className='App'>
        {bLoading && <Loading/>}

        {bNav && <Header/>}
      
 <Switch>
        <Route path="/home" component={Home}/>
        <Route path="/follow" component={Follow}/>
        <Route path="/cart" component={Cart}/>
        <Auth path="/user" component={User}/>
        <Route path="/login" component={Login}/>
        <Route path="/reg" component={Reg}/>
        <Route path="/detail/:id" component={Detail}/>
        {/* <Redirect exact from='/' to='/home'/> */}
        <Route component={ErrorPage}/>
  </Switch>
   {bFoot ?  <Footer/> : null}
 
      </div>
    )
  }
}

const initMapStateToProps=state=>({
  bNav:state.bNav,
  bFoot:state.bFoot,
  bLoading:state.bLoading
});
const initMapDispatchToProps=dispatch=>({
  viewNav:(bl)=>dispatch({type:VIEW_NAV,payload:bl}),
  viewFoot:(bl)=>dispatch({type:VIEW_FOOT,payload:bl})
});

export default connect(
  initMapStateToProps,
  initMapDispatchToProps
)(App)