import React from 'react';
import ReactDOM from 'react-dom';
import App from './layouts/App'
import './assets/css/ydui.css'
import './assets/css/style.css'
import './assets/css/reset.css'
import './assets/js/ydui.flexible.js'
import './assets/css/box-flex.css'
import './assets/css/iconfont.css'
import './assets/css/common.css'

import {BrowserRouter,Route} from 'react-router-dom'

// import axios from 'axios';
// React.Component.axios=axios;
//配置 axios
import './plugins/axios'

//配置前端的baseUrl
React.Component.baseUrl = "http://47.96.236.184:3001";

//配置utils
import * as utils from './utils';
Object.keys(utils).forEach(key=>React.Component[key]=utils[key]);

//引入redux
import store from './store';
import {Provider} from 'react-redux'



//取出再同步
let local = window.localStorage.getItem('news_user');
if (local){
  store.dispatch({type:'UPDATE_USER',payload:JSON.parse(local)})
}

ReactDOM.render(

<Provider store={store}>
    <BrowserRouter>
    <Route component={App} />, 
    </BrowserRouter>
</Provider>
,
document.getElementById('root')
);


