import React from 'react'
import {NavLink} from "react-router-dom";
import {Tabs} from 'antd-mobile'
export default class Footer extends React.Component{
    render(){
        return(
          <div className="m-tabbar">
            <NavLink to='/home' className="tabbar-item" activeClassName='tabbar-active'>    
              <span className="tabbar-icon">
                 <i className="icon-home-outline"></i>
              </span>
              <span className="tabbar-txt">首页</span>  
            </NavLink>

            <NavLink to='/follow' className="tabbar-item" activeClassName='tabbar-active'>    
              <span className="tabbar-icon">
                  <i className="iconfont icon2-unie623"></i>                 
              </span>
              <span className="tabbar-txt">商家</span>
              </NavLink>
         
              <NavLink to='/cart'  className="tabbar-item" activeClassName='tabbar-active'>    
       
              <span className="tabbar-icon">
                  <i className="icon-shopcart-outline"></i>
             
              </span>
              <span className="tabbar-txt">购物车</span>

          </NavLink>

          <NavLink to='/user' className="tabbar-item" activeClassName='tabbar-active'>    
        
              <span className="tabbar-icon">
                  <i className="iconfont icon2-icon2"></i>                 
              </span>
              <span className="tabbar-txt">我的</span>
              </NavLink>
          
      </div>
        )
    }
}